import React, {Component} from 'react';
import {signup} from "../../services/userWS";
import {Link} from 'react-router-dom';

export default class SignupContainer extends Component {
    // state === minibase de datos para la clase e hijos en caso de que los herede (o s elos pase).
    state = {
        data:{}
    }
    // esta funcion es para escucjar todo lo que estemos tecleando y poder guardarlo en data (esta en el state)
    //para puros input
    handleChange = (event) => {
        // {key:value}
        const {value,name} = event.target;
        let {data} = this.state
        
        data[name]=value

        this.setState({ data })
    }

     // esta funcion es para enviar los datos a la base de datos y validar
     //esto va en el form 
     onSubmit = (event) => {
         event.preventDefault()

         // voy a destructurar los pros
         const {history} = this.props;

         signup(this.state.data)
         .then((response)=>{
             // si todo ssle bien
            //mostramos un mensaje y mandamos a la siguiente seccion
            this.setState({data :{}});
            console.log("Felicidades", response)
            history.push("/login")

         }).catch ((error)=> {
             console.log("hay un error", error)
         })
         this.setState({data:{}})
     }


    render(){
        // aqui podemos declarar const var y let
        const {handleChange, onSubmit} = this;
        const {data} = this.state;
        return(
            <section className="uk-section">
                            <div className="uk-container uk-flex uk-flex-center">
                                <div className="uk-width-1-4">
                                    <h3>Regístrate </h3>
                                    <form
                                    //método ={()=> onSubmit(event)}
                                    onSubmit={onSubmit}
                                    className="uk-width-1-1 uk-form-stacked uk-flex uk-flex-center uk-flex-column"
                                    >
                                        <div className="uk-margin">
                                            <div className="uk-inline">
                                                <span className="uk-form-icon" uk-icon="icon: user"></span>
                                                <input 
                                                className="uk-input" 
                                                type="email" 
                                                name="email" 
                                                onChange={handleChange}
                                                required
                                                value={data["email"] ? data["email"] : "" }
                                                />
                                            </div>
                                        </div>
                                        <div className="uk-margin">
                                            <div className="uk-inline">
                                                <span className="uk-form-icon" uk-icon="icon: user"></span>
                                                <input 
                                                className="uk-input" 
                                                type="text" 
                                                name="name" 
                                                onChange={handleChange}
                                                required
                                                value={data["name"] ? data["name"] : "" }
                                                />
                                            </div>
                                        </div>

                                        <div className="uk-margin">
                                            <div className="uk-inline">
                                                <span className="uk-form-icon uk-form-icon-flip" uk-icon="icon: lock"></span>
                                                <input 
                                                className="uk-input" 
                                                type="password"
                                                name="password"
                                                onChange={handleChange}
                                                required
                                                value={data["password"] ? data["password"] : ""}
                                                />
                                            </div>
                                            <div className="uk-margin">
                                            <div className="uk-inline">
                                                <span className="uk-form-icon" uk-icon="icon: lock"></span>
                                                <input 
                                                className="uk-input" 
                                                type="password" 
                                                name="confirmPassword" 
                                                onChange={handleChange}
                                                required
                                                value={data["confirmPassword"] ? data["confirmPassword"] : "" }
                                                />
                                            </div>
                                        </div>
                                        </div>
                                        <div className="uk-text-meta">
                                          Si ya tienes cuenta... {" "}
                                          <Link className="uk-text-primary" to="/login">
                                             Ingresa
                                          </Link>
                                        </div>
                                        <button className="uk-button uk-button-primary">
                                            Terminar registro
                                        </button>
                                    </form>
                                </div>
                            </div>
                        </section>
        );
    }
}